
import {
  Box,
  Card,
  Divider,
  CardContent,
  CardHeader,
  Grid, TextField, FormControl, Select, InputLabel, MenuItem, styled, useTheme
} from '@mui/material';
import { useState } from 'react';
import { LoadingButton } from '@mui/lab';
import useAuth from 'app/hooks/useAuth';
import { Formik } from 'formik';
import { useNavigate } from 'react-router-dom';
import * as Yup from 'yup';
import axios from 'axios';
import swal from 'sweetalert';
import { Breadcrumb } from "app/components";

const Container = styled("div")(({ theme }) => (
  {
    margin: "30px",
    [theme.breakpoints.down("sm")]: { margin: "16px" },
    "& .breadcrumb": {
      marginBottom: "30px",
      [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
    },
  }
));
const ContentBox = styled(Box)(() => ({
  height: '100%',
  padding: '32px',
  position: 'relative',
  // background: 'rgba(0, 0, 0, 0.01)',
}));

const FlexBox = styled(Box)(() => ({ display: 'flex', alignItems: 'center' }));

const JustifyBox = styled(FlexBox)(() => ({ justifyContent: 'center' }));

// inital login credentials
const initialValues = {
  name: '',
  commission: '0',
  status: 'active',
};

// form field validation schema
const validationSchema = Yup.object().shape({
  name: Yup.string()
    .min(3, 'Medicine Brand Name must be 3 character length')
    .required('Medicine Brand Name is required!'),
  commission: Yup.string().required('Default commission is required!'),
  status: Yup.string().required('Status is required!'),
});

const AddMedicineBrand = () => {
  const theme = useTheme();
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);

  const { login } = useAuth();

  /**
   * Handle Form submit
   * 
   * @param {*} values 
   * @param {*} errors 
   */
  const handleFormSubmit = async (values, errors) => {
    setLoading(true);
    try {
      axios.get('/sanctum/csrf-cookie').then(response => {
        axios.post('/api/admin/add-medicine-brand', {
          name: values.name,
          status: values.status,
        })
          .then(response => {
            if (response.data.response_code === 200) {
              swal({
                title: "Done!",
                text: response.data.message,
                icon: "success",
              });
              navigate('/dashboard/medicine-brands/list');
            } else if (response.data.response_code == 412) {
              swal({
                title: "Errors!",
                text: response.data.message,
                icon: "error",
              });
            } else if (response.data.response_code == 401) {
              swal({
                title: "Warning!",
                text: response.data.message,
                icon: "warning",
              });
              errors.setErrors(response.data.errors);
            } else {
              errors.setErrors(response.data.errors);
            }
            setLoading(false);
          })
          .catch(function (error) {
            console.error(error);
          });
      });
    } catch (e) {
      setLoading(false);
    }
  };

  return (
    <>
      <Container className="add -college-course">
        <Box className="breadcrumb">
          <Breadcrumb routeSegments={[{ name: "dashboard", path: "/dashboard" }, { name: "Add Medicine Brand" }]} />
        </Box>
        <Grid container spacing={2}>
          <Grid item sm={8} xs={12}>
            <Card className="card" >
              {/* <Container className="card" > */}
              <CardHeader
                // avatar={<GroupAddIcon>U</GroupAddIcon>}
                title="Add Medicine Brand"
                titleTypographyProps={{
                  variant: "h4",
                  fontSize: '16px',
                  fontWeight: '600',
                  textTransform: 'uppercase',
                  textAlign: 'center'
                }}
              />
              <Divider />
              <CardContent>
                <Formik
                  onSubmit={(values, errors) => {
                    handleFormSubmit(values, errors);
                  }
                  }
                  initialValues={initialValues}
                  validationSchema={validationSchema}
                >
                  {({ values, errors, touched, handleChange, handleBlur, handleSubmit }) => (
                    <form onSubmit={handleSubmit}>
                      <TextField
                        fullWidth
                        // size="small"
                        type="text"
                        name="name"
                        label="Medicine Brand Name"
                        variant="outlined"
                        onBlur={handleBlur}
                        value={values.name}
                        onChange={handleChange}
                        helperText={touched.name && errors.name}
                        error={Boolean(errors.name && touched.name)}
                        sx={{ mb: 3 }}
                      />

                      <FormControl fullWidth
                        sx={{ mb: 3 }}
                      >
                        <InputLabel id="demo-simple-select-label">Status</InputLabel>
                        <Select
                          labelId="status-label"
                          id="status"
                          name="status"
                          label="Status"
                          onChange={handleChange}
                          variant="outlined"
                          onBlur={handleBlur}
                          value={values.status}
                          // helperText={touched.status && errors.status}
                          error={Boolean(errors.status && touched.status)}
                        >
                          <MenuItem value="active">Active</MenuItem>
                          <MenuItem value="in-active">In Active</MenuItem>
                        </Select>
                      </FormControl>
                      <LoadingButton
                        type="submit"
                        color="primary"
                        loading={loading}
                        variant="contained"
                        sx={{ my: 2 }}
                      >
                        Submit
                      </LoadingButton>
                    </form>
                  )}
                </Formik>
              </CardContent>
              {/* </Container> */}
            </Card>
          </Grid>
          <Grid item sm={4} xs={12}>

          </Grid>
        </Grid>
      </Container>
    </>
  );
};

export default AddMedicineBrand;
